@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');

.container{
    min-height:100vh;
    display:flex;
    justify-content:center;
    align-items:center;
    background-color:#ffffff;
    min-width: 100%;
}
.container .card{
    height:630px;
    width:950px;
    background-color:#fff;
    position:relative;
    box-shadow:0 15px 30px rgba(0,0,0,0.1);
    font-family: 'Poppins', sans-serif;
}
.container .card .form{
    width:100%;
    height:100%;
    display:flex;
}
.container .card .left-side{
    width:60%;
    height:100%;
    overflow:hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.left-side_content{
    margin: 0rem 5rem;
}

.heading{
    width: 90%;
}

.heading text{
    display: flex;
    text-align: center;
    font-weight: 700;
    font-size: 45.12px;
    line-height: 65.62px;
    color: #EDA415;
}

.input-text{
    position:relative;
    margin-top:20px;
    width:100%;
}
input[type="text"]{
    height:45px;
    width:100%;
    border:none;
    border-radius:7px;
    background-color:#f5f5f5;
    outline:0;
    padding:0 10px;
    font-size:13px;
    padding-left:30px;
}
input[type="password"]{
    height:45px;
    width:100%;
    border:none;
    border-radius:7px; 
    background-color:#f5f5f5;
    outline:0; 
    padding:0 10px;
    font-size:13px;
    padding-left:30px;
    padding-right:30px;
}

.input-text i{
    position:absolute;
    top:17px;
    font-size:13px;
    cursor:pointer;
}

.input-text .fa-user{
    left:8px;
}

.input-text .fa-envelope{
    left:8px;
}
.input-text .fa-lock{
    position:absolute;
    left:8px;
    font-size:14px;
}
.input-text .fa-eye-slash{
    position:absolute;
    right:10px;
    
}
.input-text .fa-eye{
    position:absolute;
    right:10px;
}

.forget_pass{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1.5rem 0rem;
}

.forget_pass text{
    text-decoration: underline;
    font-weight: 700;
    font-size:14px;
    color:rgb(0, 0, 0);
    cursor:pointer;
}
.button,.reg-button{
    display:flex;
    justify-content:center;
    align-items:center;
    margin-top:20px;
    
}
.button button , .reg-button button{
    background-color:#EDA415;
    border:none;
    border-radius:43.93px;
    color:#fff;
    cursor:pointer;
    font-size:15px;
    font-weight: 700;
    transition:all 0.5s;
    padding: 1rem 4rem;
}
.button button:hover{
    background-color:#f5c056;
}

.container .card .right-side{
    width:40%;
    background-color: #003F62;
    height:100%;
  
}

.right-side-text{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    height: 100%;
    color: white;
  }
  
  .right-side-text text{
  font-weight :700;
  font-size :50.78px;
  line-height :69.22px;
  }
  
  .right-side-text p{
      margin-top: 1rem;
      font-weight: 400;
  }
  
  .right-side-text button{
    background-color: transparent;
    border-color: #ffffff;
    font-size: 15px;
    font-weight: 800;
    padding: 12px 45px;
    letter-spacing: 1px;
    border-radius:43.93px;
    color: white;
    margin-top: 3rem;
  
  }

  .reg-button_or{
    display: none;
  }

  .reg-button{
    display: none;
  }

  .signin-error-message{
    color: rgb(255, 0, 0);
    font-weight: 700;
    padding-left: 1rem;
    border-radius: 0.1rem;
}

.signin-error{
    color: red;
    font-size: 14px;
    font-weight: 600;
    font-family: fantasy;
}

  @media (max-width:900px) {
    .left-side_content{
        margin: 0rem 2rem;
    }
  }


@media (max-width:750px) {
    /* .container .card {
        max-width: 350px;
        height:auto;
    } */
    .container .card .left-side {
        width:100%;
       
    }

    .heading{
        width: 100%;
    }

    .heading text{
        font-size: 35px;
        line-height: 50px;
    }

    .container .card .right-side {
        display: none;
    }

    .forget_pass{
        display: flex;
        justify-content: flex-end;
    }

    .forget_pass text{
        color: #003F62;
        font-size:12px;
    }
    
    .button button,.reg-button button{
        padding: 0.8rem 5rem;
    }

    .reg-button{
        display: flex;
    }

    .reg-button button{
        background-color: #f0bf5d;
    }

    .reg-button_or{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 1rem;
        font-weight: 700;
        font-size: 18px;
        color: #EDA415;
    }
}